<template>
  <div id="app">
    <template v-if="isIntro">
      <p>При идентификации, посмотрите в камеру и моргните минимум 4 раза</p>
      <p>Избегайте попадания в кадр других людей и слишком тёмного фона.</p>

      <button class="btn" @click="start">
        Идентификация
      </button>
    </template>

    <template v-else-if="showProgress && !testInProgress">
      <p>
        Идентификация пройдена
      </p>

      <div class="preview">
        <img class="preview__image" :src="resultPhoto" alt="Фото для отправки">
      </div>

      <button class="btn" @click="restart">
        Повторить идентификацию
      </button>
    </template>

    <template v-else>
      <p v-if="isVideoLoading">
        Загружаем видеопоток...
      </p>

      <video
        ref="videoSrc"
        class="webcam-video hidden"
        :width="videoWidth"
        :height="videoHeight"
        @canplay="onSourceReady"
      >
        Ваш браузер не поддерживает функционал этой страницы
      </video>
    </template>
  </div>
</template>

<script>
// https://github.com/webrtc/adapter
import 'webrtc-adapter';

// https://github.com/tensorflow/tfjs-models/tree/master/face-landmarks-detection
import * as tensorflow from '@tensorflow/tfjs-core';
import '@tensorflow/tfjs-backend-webgl';
import * as faceLandmarksDetection from '@tensorflow-models/face-landmarks-detection';

const indicateBlink = 0.2; // Порог EAR для определения моргания
const numberFramesEARBelowThreshold = 1; // количество кадров, когда глаза закрыты и ниже порога моргания

let framesCount = 0; // счётчик кадров с морганием
let blinksCount = 0; // общее количество морганий за время выполнения скрипта

let model;
let processTimer = null;
let stream = null;

export default {
  name: 'BlinkDetector',
  data: () => ({
    isIntro: true,
    testInProgress: true,
    showProgress: false,
    isVideoLoading: true,
    videoWidth: 0,
    videoHeight: 0,
    resultPhoto: null,
  }),
  async mounted() {
    await tensorflow.setBackend('webgl');

    if (!model) {
      model = await faceLandmarksDetection.load(
        faceLandmarksDetection.SupportedPackages.mediapipeFacemesh,
        {
          shouldLoadIrisModel: true,
          maxFaces: 1,
          iouThreshold: 0,
          scoreThreshold: 0.9,
        },
      );
    }

    const constraints = {
      video: true,
      audio: false,
      width: {
        min: 480,
        ideal: 720,
        max: 1080,
      },
      height: {
        min: 480,
        ideal: 720,
        max: 1080,
      },
      advanced: [
        {
          facingMode: 'user',
          aspectRatio: 0.75,
        },
      ],
    };

    stream = await navigator.mediaDevices.getUserMedia(constraints);
  },
  beforeDestroy() {
    this.stopCamera();
  },
  methods: {
    start() {
      this.isIntro = false;
      this.startCamera();
    },
    startCamera() {
      this.$nextTick(() => {
        const video = this.$refs.videoSrc;

        video.setAttribute('autoplay', '');
        video.setAttribute('muted', '');
        video.setAttribute('playsinline', '');

        video.srcObject = stream;

        video.onloadedmetadata = () => {
          video.play();
        };
      });
    },
    stopCamera() {
      if (!stream) {
        return;
      }

      cancelAnimationFrame(processTimer);
      this.$refs.videoSrc.pause();

      this.$refs.videoSrc.srcObject = null;
      stream.getVideoTracks()[0].stop();
      this.testInProgress = false;
      this.isVideoLoading = true;
    },
    restart() {
      this.stopCamera();

      framesCount = 0;
      blinksCount = 0;
      this.resultPhoto = null;
      this.showProgress = false;
      this.testInProgress = true;

      this.startCamera();
    },
    async onSourceReady() {
      this.videoWidth = this.$refs.videoSrc.videoWidth;
      this.videoHeight = this.$refs.videoSrc.videoHeight;

      await this.processVideo();
      this.$refs.videoSrc.classList.remove('hidden');
      this.isVideoLoading = false;
    },
    eyeAspectRatio(eye) {
      return (Math.abs(eye.p2 - eye.p6) + Math.abs(eye.p3 - eye.p5)) / (2 * Math.abs(eye.p1 - eye.p4));
    },
    async processVideo() {
      if (!this.$refs.videoSrc) {
        console.warn('Видеопоток не найден');

        return;
      }

      const predictions = await model.estimateFaces({
        input: this.$refs.videoSrc,
        returnTensors: false,
        flipHorizontal: false,
        predictIrises: true,
      });

      const faceInViewConfidence = predictions[0] ? predictions[0].faceInViewConfidence : 0;
      const annotations = predictions[0] ? predictions[0].annotations : {};

      // распознавание глаз
      if (faceInViewConfidence > 0.9 && Object.keys(annotations).length > 0) {
        // Точки глаз. Ссылка на изображение, следовательно, противоположное реальному (например, слева на изображении -> правый глаз)
        const leftEye = {
          p1: annotations.rightEyeLower0[8][0],
          p2: annotations.rightEyeUpper0[4][1],
          p3: annotations.rightEyeUpper0[3][1],
          p4: annotations.rightEyeLower0[0][0],
          p5: annotations.rightEyeLower0[3][1],
          p6: annotations.rightEyeLower0[4][1],
        };

        const rightEye = {
          p1: annotations.leftEyeLower0[8][0],
          p2: annotations.leftEyeUpper0[4][1],
          p3: annotations.leftEyeUpper0[3][1],
          p4: annotations.leftEyeLower0[0][0],
          p5: annotations.leftEyeLower0[3][1],
          p6: annotations.leftEyeLower0[4][1],
        };

        const leftEAR = this.eyeAspectRatio(leftEye);
        const rightEAR = this.eyeAspectRatio(rightEye);
        const EAR = (leftEAR + rightEAR) / 2;

        if (EAR < indicateBlink) {
          framesCount += 1;
        }
        else {
          if (framesCount >= numberFramesEARBelowThreshold) {
            blinksCount += 1;
          }

          framesCount = 0;

          const randomCountBlink = blinksCount >= (Math.floor(Math.random() * Math.floor(4)));

          if (!this.resultPhoto && randomCountBlink) {
            // Делаем снимок пользователя, чтобы не было возможности подсунуть фото
            const canvas = document.createElement('canvas');
            canvas.width = this.videoWidth;
            canvas.height = this.videoHeight;
            const canvasContext = canvas.getContext('2d');
            canvasContext.drawImage(this.$refs.videoSrc, 0, 0, this.videoWidth, this.videoHeight);
            this.resultPhoto = canvas.toDataURL('image/jpeg');
            canvas.remove();
          }
        }
      }

      processTimer = requestAnimationFrame(this.processVideo);

      if (blinksCount >= 4) {
        this.stopCamera();
        this.showProgress = true;

        navigator.vibrate([300, 300, 300]);
      }
    },
  },
};
</script>

<style>
.btn {
  margin: 10px auto;
  padding: 8px 20px;
}

.webcam-video {
  display: inline-block;
  width: 100%;
  height: auto;
}

.hidden {
  display: none;
  visibility: hidden;
}

.preview {
  display: block;
  width: 280px;
  height: 370px;
  margin: 20px auto;
  padding: 0;
  border-radius: 4px;
  user-select: none;
  overflow: hidden;
}

.preview__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
</style>
